"use client";

import { use, useEffect, useMemo, useState } from "react";
import Script from "next/script";

type Track = {
  id: string;
  name: string;
  artist: string;
  artworkUrl?: string;
  votes: number;
  addedAt: number;
};

type RoomState = {
  id: string;
  createdAt: number;
  mode: "voted" | "shuffle";
  queue: Track[];
  nowPlaying?: Track;
  isHost: boolean;
};

declare global {
  interface Window {
    MusicKit?: any;
  }
}

function getOrMakeClientId() {
  const k = "aux_client_id";
  const existing = localStorage.getItem(k);
  if (existing) return existing;

  const id =
    (globalThis.crypto && "randomUUID" in globalThis.crypto)
      ? globalThis.crypto.randomUUID()
      : `${Date.now()}_${Math.random().toString(16).slice(2)}`;

  localStorage.setItem(k, id);
  return id;
}

export default function RoomPage(props: { params: Promise<{ roomId: string }> }) {
  const { roomId: raw } = use(props.params);
  const roomId = useMemo(() => (raw ?? "").toUpperCase(), [raw]);

  const [room, setRoom] = useState<RoomState | null>(null);
  const [q, setQ] = useState("");
  const [results, setResults] = useState<any[]>([]);
  const [mkReady, setMkReady] = useState(false);
  const [clientId, setClientId] = useState("");

  useEffect(() => {
    setClientId(getOrMakeClientId());
  }, []);

  // SSE realtime
  useEffect(() => {
    if (!roomId) return;
    const es = new EventSource(`/api/rooms/${roomId}/events`);
    es.onmessage = (evt) => {
      try {
        const data = JSON.parse(evt.data);
        if (!data?.error) setRoom(data);
      } catch {}
    };
    return () => es.close();
  }, [roomId]);

  async function configureMusicKit() {
    const res = await fetch("/api/apple-music/developer-token", { cache: "no-store" });
    const { token: developerToken } = await res.json();
    await window.MusicKit.configure({
      developerToken,
      app: { name: "Aux Reimagined", build: "1.0.0" },
    });
    setMkReady(true);
  }

  async function connectAppleMusic() {
    const music = window.MusicKit.getInstance();
    await music.authorize();
  }

  async function search() {
    const term = q.trim();
    if (!term) return;
    const res = await fetch(`/api/apple-music/search?q=${encodeURIComponent(term)}&storefront=us`, { cache: "no-store" });
    const json = await res.json();
    setResults(json?.results?.songs?.data ?? []);
  }

  async function addSong(item: any) {
    if (!clientId) return;

    const attr = item.attributes;
    const artwork = attr?.artwork?.url
      ? attr.artwork.url.replace("{w}", "128").replace("{h}", "128")
      : undefined;

    const res = await fetch(`/api/rooms/${roomId}/add`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-client-id": clientId,
      },
      body: JSON.stringify({
        id: item.id,
        name: attr?.name ?? "Unknown",
        artist: attr?.artistName ?? "Unknown",
        artworkUrl: artwork,
      }),
    });

    if (!res.ok) {
      const txt = await res.text();
      alert(`Add failed (${res.status}).\n${txt}`);
    }
  }

  async function vote(trackId: string, delta: 1 | -1) {
    if (!clientId) return;

    const res = await fetch(`/api/rooms/${roomId}/vote`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-client-id": clientId,
      },
      body: JSON.stringify({ trackId, delta }),
    });

    if (!res.ok) {
      const txt = await res.text();
      alert(`Vote failed (${res.status}).\n${txt}`);
    }
  }

  async function playNextOrSkip() {
    const res = await fetch(`/api/rooms/${roomId}/next`, { method: "POST" });
    if (res.status === 403) {
      alert("Only the room creator can skip/play next.");
      return;
    }
    const json = await res.json();
    const next = json?.next as Track | null;

    if (!next) {
      alert("Queue is empty.");
      return;
    }

    try {
      const music = window.MusicKit.getInstance();
      await music.setQueue({ song: next.id });
      await music.play();
    } catch (e) {
      console.error(e);
      alert("Playback failed. Click 'Connect Apple Music' first.");
    }
  }

  async function toggleMode() {
    const mode = room?.mode === "shuffle" ? "voted" : "shuffle";
    const res = await fetch(`/api/rooms/${roomId}/mode`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ mode }),
    });

    if (res.status === 403) {
      alert("Only the room creator can change mode.");
      return;
    }
    if (!res.ok) {
      const txt = await res.text();
      alert(`Mode change failed (${res.status}).\n${txt}`);
    }
  }

  async function remove(trackId: string) {
    const res = await fetch(`/api/rooms/${roomId}/remove`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ trackId }),
    });

    if (res.status === 403) {
      alert("Only the room creator can remove songs.");
      return;
    }
    if (!res.ok) {
      const txt = await res.text();
      alert(`Remove failed (${res.status}).\n${txt}`);
    }
  }

  async function copyInvite() {
    const url = `${window.location.origin}/room/${roomId}`;
    try {
      await navigator.clipboard.writeText(url);
      alert("Invite link copied.");
    } catch {
      alert(url);
    }
  }

  const isHost = !!room?.isHost;

  return (
    <div>
      <Script
        src="https://js-cdn.music.apple.com/musickit/v1/musickit.js"
        strategy="afterInteractive"
        onLoad={() => configureMusicKit()}
      />

      {/* Top card: room info + controls */}
      <div className="card" style={{ marginBottom: 14 }}>
        <div className="row">
          <div className="pill">Room</div>
          <div className="title">{roomId}</div>
          <button className="secondary" onClick={copyInvite}>Copy invite</button>
        </div>

        <div className="spacer" />

        <div className="row">
          <button disabled={!mkReady} onClick={connectAppleMusic}>Connect Apple Music</button>
          <button onClick={playNextOrSkip} disabled={!isHost}>
            {isHost ? "Play next / Skip" : "Host only"}
          </button>
          <button className="secondary" onClick={toggleMode} disabled={!isHost}>
            Mode: {room?.mode ?? "voted"}
          </button>
        </div>

        {!isHost ? (
          <div className="muted" style={{ textAlign: "center", marginTop: 10 }}>
            Only the creator of this room can control playback and moderation.
          </div>
        ) : null}
      </div>

      {/* Two-column layout */}
      <div className="grid2">
        {/* LEFT: Search */}
        <div className="card">
          <div className="title" style={{ textAlign: "center" }}>Search</div>
          <div className="spacer" />

          {/* Centered search bar */}
          <div style={{ display: "flex", justifyContent: "center" }}>
            <div style={{ width: "min(560px, 100%)", display: "flex", gap: 10 }}>
              <input
                value={q}
                onChange={(e) => setQ(e.target.value)}
                placeholder="Search songs..."
                onKeyDown={(e) => { if (e.key === "Enter") search(); }}
              />
              <button onClick={search}>Search</button>
            </div>
          </div>

          <div className="spacer" />
          <div className="muted" style={{ textAlign: "center" }}>
            Results show below. Click “Add” to queue.
          </div>

          <div className="spacer" />

          <div style={{ display: "grid", gap: 10 }}>
            {results.map((item) => (
              <div key={item.id} className="item">
                {item.attributes?.artwork?.url ? (
                  <img
                    src={item.attributes.artwork.url.replace("{w}", "96").replace("{h}", "96")}
                    width={56}
                    height={56}
                    alt=""
                  />
                ) : null}

                <div style={{ flex: 1, minWidth: 0 }}>
                  <div className="title" style={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
                    {item.attributes?.name}
                  </div>
                  <div className="sub" style={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
                    {item.attributes?.artistName}
                  </div>
                </div>

                <button className="secondary" onClick={() => addSong(item)}>Add</button>
              </div>
            ))}

            {results.length === 0 ? (
              <div className="muted" style={{ textAlign: "center" }}>No results yet.</div>
            ) : null}
          </div>
        </div>

        {/* RIGHT: Now Playing + Queue */}
        <div className="card">
          <div className="title">Now playing</div>
          <div className="spacer" />

          {room?.nowPlaying ? (
            <div className="item">
              {room.nowPlaying.artworkUrl ? <img src={room.nowPlaying.artworkUrl} width={56} height={56} alt="" /> : null}
              <div style={{ flex: 1, minWidth: 0 }}>
                <div className="title" style={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
                  {room.nowPlaying.name}
                </div>
                <div className="sub" style={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
                  {room.nowPlaying.artist}
                </div>
              </div>
              <div className="pill">{room.nowPlaying.votes}</div>
            </div>
          ) : (
            <div className="muted">Nothing yet.</div>
          )}

          <div className="spacer" />
          <div className="title">Queue</div>
          <div className="spacer" />

          <div style={{ display: "grid", gap: 10 }}>
            {(room?.queue ?? []).map((t) => (
              <div key={t.id} className="item">
                {t.artworkUrl ? <img src={t.artworkUrl} width={56} height={56} alt="" /> : null}
                <div style={{ flex: 1, minWidth: 0 }}>
                  <div className="title" style={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
                    {t.name}
                  </div>
                  <div className="sub" style={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
                    {t.artist}
                  </div>
                </div>

                <div className="pill">{t.votes}</div>
                <button className="secondary" onClick={() => vote(t.id, 1)}>+1</button>
                <button className="secondary" onClick={() => vote(t.id, -1)}>-1</button>

                {isHost ? (
                  <button className="danger" onClick={() => remove(t.id)}>Remove</button>
                ) : null}
              </div>
            ))}

            {room && room.queue.length === 0 ? (
              <div className="muted">Queue is empty.</div>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
}
